

p {
  font-size: 14px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.top-box {
  width: 100%;
  border: 1px solid black;
  padding: 4px;
  border-radius: 5px;
  background-color: white;
}

.middle-box {
  border: 1px solid black;
  width: 100%;
  padding: 4px;
  border-radius: 5px;
  background-color: white;
}

.bottom-box {
  border: 1px solid black;
  width: 100%;
  padding: 4px;
  border-radius: 5px;
  background-color: white;
}

/* TOP CONTIANER */

.title-container {
  width: 100%;
  display: flex; 
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.showjumping-title {
  font-family: 'Lilita One', cursive;
}

.title-image {
  width: 50%;
  padding-bottom: 30px;
}

.resized-image {
  max-width: 100%;
  max-height: 100%;
  border-radius: 30px;
}


/* TIMER DISPLAY */
.timer-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  width: 80%;
  
  border-radius: 4px;
}

#timer {
    font-size: 4rem;
    border-radius: 4px;
}

.timer-buttons-container {
  display: flex;
  justify-content: space-between;
  width: 80%;
  padding: 10px;
}

.timer-display {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  flex-direction: column;
}

/* WINNER COMPONENT */
.winners-container {
  border: 1px solid black;
  
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 20px;
  align-items: flex-end;
  padding: 10px;

}

.third-place-container{
  height: 75px;
  background-color: green;
  color: white;
}
.second-place-container{
  height: 100px;
  background-color: green;
  color: white;
}
.first-place-container{
  height: 130px;
  background-color: gold;
}

.winner-ranked-container {
  font-family: 'Lilita One', cursive;
  padding: 4px;
  border: 1px solid black;
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center; 
  justify-content: end;
  border-radius: 5px;
}

.winner-ranking-number {
  font-weight: 400;
}

/* .winner-icon {
  font-size: 50px;
} */

/* MIDDLE SECTION */
.end-reset-comp-buttons-container {
  display: flex;
  justify-content: space-around;
  padding: 5px;
}

.title {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.results-table-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

/* RESULTS TABLE */

table {
  border: 1px solid black;
  width: 90%;
  border-radius: 4px;
  padding: 5px;
  margin: 5px;
}

thead {
  background-color: #61dafb;
}

tbody {
  background-color: aliceblue;
}

td {
  text-align: center; /* Center the text horizontally */
  vertical-align: middle; /* Center the text vertically */
}

/* Bottom Container */

.add-riders-container {
  padding: 10px;
  display: flex; 
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.start-list-container {

  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

/* Start list Table */

.start-list-table {

  width: fit-content;
  font-size: 25px;
  border: 0;
}

.start-list-tbody {
  background-color: white;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
